<template>
    <div class="wrap-content">
        <div class="filter-section row-min-space">
            <div class="row">
                <div class="col-md-6 filter-title-section">
                    <h2>Roles & Permissions</h2>
                </div>
                <div class="col-md-6 text-right">
                    <div class="row">
                        <div class="col-md-6 col-xs-12">
                            <base-search
                                placeholder="Search role"
                            ></base-search>
                        </div>
                        <div class="col-md-6 col-xs-12 filter-btn-section">
                            <div class="row">
                                <div class="col-md-5">
                                    <base-button
                                        btnLabel="Apply"
                                        btnType="submit"
                                        btnVariant="primary"
                                    ></base-button>
                                </div>
                                <div class="col-md-7">
                                    <base-button
                                        btnLabel="+ Add Role"
                                        btnType="submit"
                                        btnVariant="secondary btn-block"
                                        @click="addModify('Add Role', 'Create')"
                                        class="secondary-add-btn"
                                    ></base-button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <!-- user management table -->
        <div class="table-section tablet-table-xs-min-width">
            <b-table :items="items" :fields="fields" class="user-roles-table">
                <!-- status slot -->
                <template slot="status" slot-scope="data">
                    <base-status :statusType="data.value"></base-status>
                </template>
                <!-- action slot -->
                <template slot="action" slot-scope="data">
                    <div
                        :class="
                            data.item.status[0].label == 'Locked'
                                ? 'disabled'
                                : ''
                        "
                        class="action-review"
                    >
                        <!-- if action modify -->
                        <base-action
                            v-if="
                                data.value == 'active' ||
                                    data.value == 'archive'
                            "
                            icon="icon-edit"
                            label="Modify"
                            @click="addModify('Modify Role', 'Update')"
                        ></base-action>
                        <!-- if action archive -->
                        <base-action
                            v-if="data.value == 'active'"
                            icon="icon-trash"
                            label="Archive"
                            v-b-modal.v-b-modal.archive-popup
                        ></base-action>
                        <!-- if action activate -->
                        <base-action
                            v-if="data.value == 'archive'"
                            icon="icon-done"
                            label="Activate"
                            v-b-modal.activate-popup
                        ></base-action>
                    </div>
                </template>
            </b-table>
        </div>
        <pagination
            totalRecords="Showing 1 to 6 of 6 records"
            nextBtnDisable
            selectPaginationDisable
            :showRecords="recordShow"
        ></pagination>
        <add-role-popup
            :title="title"
            :buttonText="buttonText"
        ></add-role-popup>
        <archive-popup
            title="Archive Role"
            description="Are you sure you want to archive this role? You can re-activate it later."
        ></archive-popup>
        <activate-popup></activate-popup>
    </div>
</template>

<script>
import addRolePopup from '@/components/popups/AddRolePopup.vue'
import archivePopup from '@/components/popups/ArchivePopup.vue'
import pagination from '@/components/Pagination.vue'
import activatePopup from '@/components/popups/ActivatePopup.vue'
import { mapGetters } from 'vuex'
export default {
    components: {
        addRolePopup,
        archivePopup,
        pagination,
        activatePopup,
    },

    /*
    |--------------------------------------------------------------------------
    | Component > props
    |--------------------------------------------------------------------------
    */

    props: {}, // End of Component > props

    /*
    |--------------------------------------------------------------------------
    | Component > data
    |--------------------------------------------------------------------------
    */
    data() {
        return {
            selected: '1',
            fields: [],
            items: [],
            title: '',
            buttonText: '',
            recordShow: [{ text: 'Show 10 records', value: null }],
        }
    }, // End of Component > data

    /*
    |--------------------------------------------------------------------------
    | Component > computed
    |--------------------------------------------------------------------------
    */
    computed: {
        ...mapGetters(['systemUsersData']),
    }, // End of Component > computed

    /*
    |--------------------------------------------------------------------------
    | Component > methods
    |--------------------------------------------------------------------------
    */
    methods: {
        initializeData() {
            //system log table
            let tableFields = this.systemUsersData.roles.fields //get user data from store
            this.fields = tableFields //push data into array
            let tableItems = this.systemUsersData.roles.items //get user data from store
            this.items = tableItems //push data into array
        },

        addModify(e, f) {
            this.title = e
            this.buttonText = f
            this.$bvModal.show('add-role-popup')
        },
    }, // End of Component > methods

    /*
    |--------------------------------------------------------------------------
    | Component > mounted
    |--------------------------------------------------------------------------
    */
    mounted() {
        this.initializeData()
    }, // End of Component > mounted
} // End of export default
</script>
